import { Component, h, Prop } from '@stencil/core';
import { isExternalUrl } from '../../utils';
import { BUTTON_TYPE, LINK_AS, LINK_SIZE, LINK_VARIANTS } from './exports';

/**
 * @slot label - Slot for the label / content of the link
 */
@Component({
  tag: 'mdx-link',
  styleUrl: 'link.scss',
  shadow: true,
})
export class Link {
  @Prop()
  href: string;

  @Prop({ reflect: true })
  variant: (typeof LINK_VARIANTS)[keyof typeof LINK_VARIANTS] = LINK_VARIANTS.RUNNINGTEXT;

  @Prop({ reflect: true })
  size: (typeof LINK_SIZE)[keyof typeof LINK_SIZE] = LINK_SIZE.MEDIUM;

  @Prop()
  target: '_blank' | '_self' | '_parent' | '_top' = '_self';

  @Prop({ reflect: true })
  as: (typeof LINK_AS)[keyof typeof LINK_AS] = LINK_AS.LINK;

  @Prop()
  type: (typeof BUTTON_TYPE)[keyof typeof BUTTON_TYPE] = BUTTON_TYPE.BUTTON;

  private isExternalURL() {
    return isExternalUrl(this.href);
  }

  render() {
    if (this.as === LINK_AS.LINK) {
      return (
        <a class="link" href={this.href} target={this.target} rel={this.isExternalURL() ? 'noreferrer' : undefined}>
          <slot />
        </a>
      );
    }
    return (
      <button class="link" type={this.type}>
        <slot />
      </button>
    );
  }
}
